.trainers {
  padding: 60px 0;
  background: #f9f9f9;

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 40px;

  // Ñ‡ÐµÑ€Ð³ÑƒÐ²Ð°Ð½Ð½Ñ Ð½Ð°Ð¿Ñ€ÑÐ¼ÐºÑƒ Ð´Ð»Ñ ÐºÐ¾Ð¶Ð½Ð¾Ð³Ð¾ Ð´Ñ€ÑƒÐ³Ð¾Ð³Ð¾ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° (Ð½Ð° Ð´ÐµÑÐºÑ‚Ð¾Ð¿Ñ–)
  &:nth-child(even) {
    flex-direction: row-reverse;
  }

 


  // Ð°Ð´Ð°Ð¿Ñ‚Ð°Ñ†Ñ–Ñ Ð´Ð»Ñ Ð¼Ð¾Ð±Ñ–Ð»ÑŒÐ½Ð¸Ñ…
  @media (max-width: 1200px) {
    // ðŸ”¹ Ð¿ÐµÑ€ÐµÐ¿Ð¸ÑÑƒÑ” direction Ð½Ð°Ð²Ñ–Ñ‚ÑŒ Ð´Ð»Ñ Ð¿Ð°Ñ€Ð½Ð¸Ñ…
    &, &:nth-child(even) {
      flex-direction: column;
    }

    text-align: center;
    gap: 20px;
  }
}

 &__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background: #9a9a9a;
}

&__item:not(:last-child)::after {
  content: "";
  display: block;
  height: 1px;
  background: #9a9a9a;
  margin-top: 40px;
}

 &__title {
    font-size: 36px;
    text-align: center;
    margin-bottom: 40px;
    color: #333;
    font-family: var(--podkova-font);
  }


  &__img {
    width: 300px;
    border-radius: 20px;
    object-fit: cover;
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
    
    // Ð½Ð° ÐŸÐš Ð¾Ð±Ñ€Ñ–Ð·Ð°Ñ”Ð¼Ð¾ Ð¿Ð¾Ð»Ð¾Ð²Ð¸Ð½Ñƒ Ñ„Ð¾Ñ‚Ð¾ Ð¿Ð¾ Ð²Ð¸ÑÐ¾Ñ‚Ñ– (Ð²Ð³Ð¾Ñ€Ñƒ)

    @media (min-width: 1200px) {
      width: 500px;
      height: auto;
    }
  }

  

  &__text {
    width: 50%;
    padding: 0 20px;

    @media (max-width: 1200px) {
      width: 100%;
      padding: 0;
    }
  }

  &__name {
    font-size: 28px;
    font-weight: 700;
    color: #222;
    margin-bottom: 16px;
    text-align: left;

    @media (max-width: 1200px) {
      text-align: center;
    }
  }

  &__description {
    font-size: 16px;
    color: #555;
    line-height: 1.6;
    white-space: pre-line;

    @media (max-width: 1200px) {
      text-align: center;
    }
  }
}

.flexx {
  display: flex;
  flex-direction: column;
}


  .wit {
    height: 600px;

    @media screen and (min-width: 1200px) {
      width: 400px;
    }
  }