@import "../variebles";


.header {
    background-color: var(--background-color);
    border-bottom: var(--color-accent) solid 1px;
}

body {
    background-color: var(--background-color);
}
.work-schedule {
    font-family: "Inter", sans-serif;
    max-width: 300px;
    background: #fff;
    border-radius: 14px;
    padding: 18px 20px;
    box-shadow: 0 4px 14px rgba(0,0,0,0.05);
  }

  .contacts {
    margin-top: 30px;
  }

  .work-schedule h3 {
    margin-top: 0;
    font-size: 18px;
    color: #111;
    border-bottom: 1px solid #eee;
    padding-bottom: 8px;
  }

  .schedule-list {
    margin: 12px 0;
  }

  .schedule-list div {
    padding: 4px 0;
    color: #333;
    font-size: 15px;
  }

  .today-status {
    margin-top: 10px;
    font-weight: 600;
    font-size: 15px;
  }

  #today-status {
    font-weight: 700;
  }

  .open {
    color: #1aaf5d; /* Ð·ÐµÐ»ÐµÐ½Ð¸Ð¹ */
  }

  .closed {
    color: #e63946; /* Ñ‡ÐµÑ€Ð²Ð¾Ð½Ð¸Ð¹ */
  }

  span {
    color: white;
  }

  .link {
    color: black;
    background-color: yellow;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 8px;
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;
      width: max-content; 
    display: block; 
    margin-left: auto;
    margin-top: 30px;
  }

  .ramka {
    border: yellow solid 2px;
    border-radius: 10px;
    padding-left: 10px;
    padding-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    display: block;

     @media screen and (min-width: 1200px) {
        width: 500px;
padding-left: 30px;
    padding-bottom: 30px;
        }
  }

  .contacts__flex { 
    display: flex;
      align-items: center; 
      gap: 50px;
    align-items: flex-start; 
     flex-direction: column;
    
    @media screen and (min-width: 1200px) {
     flex-direction: row;
   
    }
  }

  .socials__number-phone {
    color: white;
  }

