header {
    border-bottom: var(--color-accent) solid 1px;
     box-sizing: border-box;
}

body {
    background-color: var(--background-color);
     overflow-y: scroll;
}

.history__image {
  opacity: 0;
  transform: scale(0.9);
  animation: fadeInZoom 1.2s ease forwards;
  border: yellow solid 2px;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
    display: block;
    width: 300px;

     @media screen and (min-width: 1200px) {
        width: 500px;

        }
}

@keyframes fadeInZoom {
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.history__description {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s ease;
}

.history__description.visible {
  opacity: 1;
  transform: translateY(0);
}

 .history__description,
  .history__vovk-before,
  .history__description-vovk,
  .history__vovk-after {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.8s ease, transform 0.8s ease;
  }

  .history__vovk-after {
    border: yellow solid 2px;
    border-radius: 20px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 300px;

     @media screen and (min-width: 1200px) {
